import { isEmpty } from "@/utils";
export default {
  props: {
    prop: {
      tab: {}
    }
  },
  data: function data() {
    return {
      transitionName: "slide-right",
      // selectedLabelDefault: '首页',
      selectedLabelDefault: "",
      tabs: [{
        label: "首页",
        icon: "cubeic-home",
        path: "/project/list"
      }, {
        label: "我的",
        icon: "cubeic-person",
        path: "/user/mine"
      }]
    };
  },
  watch: {
    $route: function $route(to, from) {
      this.changeHandlerByPath(to.path); // if(to.path !== from.path){
      //     this.changeHandlerByPath(to.path);
      // }
    }
  },
  methods: {
    clickHandler: function clickHandler(label) {
      // if you clicked home tab, then print 'Home'
      this.changeHandler(label);
    },
    // 点击与自身不同的其他导航
    changeHandler: function changeHandler(label) {
      // if you clicked different tab, this methods can be emitted
      switch (label) {
        case "首页":
          this.$router.push({
            path: "/project/list"
          });
          break;

        case "我的":
          this.$router.push({
            path: "/user/mine"
          });
          break;

        case "测试":
          this.$router.push({
            path: "/test"
          });
          break;
      }
    },
    changeHandlerByPath: function changeHandlerByPath(path) {
      switch (path) {
        case "/project/list":
          this.selectedLabelDefault = "首页";
          break;

        case "/user/mine":
          this.selectedLabelDefault = "我的";
          break;

        case "/test":
          this.selectedLabelDefault = "测试";
          break;

        default:
          this.selectedLabelDefault = "";
      }
    }
  },
  created: function created() {
    this.changeHandlerByPath(this.$route.path);
    var tab = this.prop.tab;

    if (!isEmpty(tab)) {
      this.tabs.push(tab);
    }
  },
  mounted: function mounted() {
    document.body.style.marginBottom = "100px";
  },
  beforeDestroy: function beforeDestroy() {
    document.body.style.marginBottom = "60px";
  }
};